import * as React from 'react';
import Img from 'gatsby-image/withIEPolyfill';
import { getFluidImageData } from './gatsbyImage.utils';

type Props = {
  asset: {
    [key: string]: any;
  };
} & React.HTMLAttributes<HTMLElement>;

const GatsbyImage: React.FunctionComponent<Props> = ({ className, ...props }) => {
  return <Img fluid={getFluidImageData(props.asset)} className={className} />;
};

export default GatsbyImage;
