import * as React from 'react';
import classNames from 'classnames';
import posed, { PoseGroup } from 'react-pose';
import Link from '../../../common/components/link/Link';
import { Treatment } from '../forces/types';
import { BodyText } from '../../../common/components/texts';
import SectionListTitle from '../../../common/components/section/SectionListTitle';
import { fadeUp } from '../../../common/animations/animationConfigs';
import styles from './TreatmentMenuList.module.css';

const ListItem = posed.li(fadeUp);

type Props = {
  treatments: Array<Treatment>;
} & React.HTMLAttributes<HTMLElement>;

const TreatmentMenuList: React.FunctionComponent<Props> = ({ className, treatments, ...props }) => {
  return (
    <ul className={classNames(styles.list, className)} {...props}>
      <PoseGroup animateOnMount>
        {treatments.map((treatment, index) => (
          <ListItem key={treatment.id} className={styles.listItem} i={index}>
            <div className={styles.titleContainer}>
              <SectionListTitle>{treatment.title}</SectionListTitle>
              <SectionListTitle className={styles.price}>{treatment.price}</SectionListTitle>
            </div>
            <BodyText>
              {treatment.description}
              <Link to={treatment.slug.current} className={styles.readMore}>
                Les mer
              </Link>
            </BodyText>
          </ListItem>
        ))}
      </PoseGroup>
    </ul>
  );
};

export default TreatmentMenuList;
