import * as React from 'react';
import classNames from 'classnames';
import { Location } from '../types';
import SectionSubtitle from '../../../common/components/section/SectionSubtitle';
import { BodyBolderText } from '../../../common/components/texts';
import styles from './LocationSelector.module.css';

type Props = {
  locations: Array<Location>;
  selectedIndex: number;
  onLocationSelected: (index: number) => void;
} & React.HTMLAttributes<HTMLElement>;

class LocationSelector extends React.Component<Props> {
  renderOpeningHoursIfAvailable() {
    const { locations, selectedIndex } = this.props;
    const selectedLocation = locations[selectedIndex];
    if (selectedLocation.openingHours == null) {
      return null;
    }
    //quickfix til vi har bestemt pningstider.
    //const { monday, friday, saturday, sunday } = selectedLocation.openingHours;

    return (
      <>
        <div className={styles.openingHoursLine}>
          <BodyBolderText>pent hver dag</BodyBolderText>
        </div>
      </>
    );
  }

  render() {
    const { className, locations, onLocationSelected, selectedIndex, ...props } = this.props;
    const selectedLocation = locations[selectedIndex];
    return (
      <div className={classNames(styles.container, className)} {...props}>
        <div className={styles.locationTitleContainer}>
          {locations.map((location, index) => (
            <SectionSubtitle
              key={location.id}
              onClick={() => onLocationSelected(index)}
              className={classNames(styles.locationTitle, {
                [styles.locationTitleSelected]: index === selectedIndex,
              })}>
              {location.name}
            </SectionSubtitle>
          ))}
        </div>
        <BodyBolderText className={styles.locationAddress}>
          {selectedLocation.name}
          <br />
          {selectedLocation.address.street}
          <br />
          {selectedLocation.address.postalCode} {selectedLocation.address.city.name}
        </BodyBolderText>
        {this.renderOpeningHoursIfAvailable()}
      </div>
    );
  }
}

export default LocationSelector;
