import * as React from 'react';
import OurCultureSection from '../../../domain/culture/containers/OurCultureSection';
import GiftCardSection from '../../../domain/user/containers/GiftCardSection';
import TreatmentMenuSection from '../../../domain/treatment/components/TreatmentMenuSection';
import LocationsSection from '../../../domain/location/containers/LocationsSection';
import NewsletterSection from '../../../domain/user/containers/NewsletterSection';
import ProductSection from '../../../domain/product/containers/ProductSection';
import { BodyText, H2, H1, H3, H4 } from '../texts';
import Checklist from '../checklist/Checklist';
import ChecklistItem from '../checklist/ChecklistItem';
import CallToActionLink from '../callToAction/CallToActionLink';
import MembershipSection from '../../../domain/user/containers/MembershipSection';
import Link from '../link/Link';
import SectionTitle from '../section/SectionTitle';
import styles from './blockContentSerializers.module.css';

interface Props {
  children: Array<any>;
  isInline: boolean;
  node: any;
  options: {
    [key: string]: any;
  };
  mark?: any;
}

export default {
  types: {
    block: (props: Props) => {
      if (props.node.style === 'sectionTitle') {
        return <SectionTitle className={styles.sectionTitle}>{props.children}</SectionTitle>;
      }
      if (props.node.style === 'h1') {
        return <H1>{props.children}</H1>;
      }
      if (props.node.style === 'h2') {
        return <H2>{props.children}</H2>;
      }
      if (props.node.style === 'h3') {
        return <H3>{props.children}</H3>;
      }
      if (props.node.style === 'h4') {
        return <H4>{props.children}</H4>;
      }
      return <BodyText className={styles.bodyText}>{props.children}</BodyText>;
    },
    cultureSection: (props: Props) => <OurCultureSection {...props.node} />,
    giftcardSection: (props: Props) => <GiftCardSection {...props.node} />,
    membershipSection: (props: Props) => <MembershipSection {...props.node} />,
    treatmentsMenuSection: (props: Props) => <TreatmentMenuSection {...props.node} />,
    locationsSection: (props: Props) => <LocationsSection {...props.node} />,
    newsletterSection: (props: Props) => <NewsletterSection {...props.node} />,
    callToAction: (props: Props) => (
      <CallToActionLink to={props.node.path}>{props.node.title}</CallToActionLink>
    ),
    product: (props: Props) => <ProductSection {...props.node} />,
  },
  marks: {
    link: (props: Props) => {
      return (
        <Link to={props.mark.href} className={styles.link}>
          {props.children}
        </Link>
      );
    },
  },
  list: (props: Props) => <Checklist>{props.children}</Checklist>,
  listItem: (props: Props) => <ChecklistItem>{props.children}</ChecklistItem>,
};
