import * as React from 'react';
import Section from '../../../common/components/section/Section';
import { Container, Row, Column } from '../../../common/components/grid/Grid';
import { H3 } from '../../../common/components/texts';
import BlockContent from '../../../common/components/blockContent/BlockContent';
import GatsbyImage from '../../../common/components/gatsbyImage/GatsbyImage';
import CallToActionButton from '../../../common/components/callToAction/CallToActionButton';
import Cart from '../../../common/components/cart/Cart';
import config from '../../../config';
import styles from './ProductSection.module.css';

type Props = {
  body: any;
  id: string;
  title: string;
  productId: string;
  price: string;
  _rawImage: {
    asset: {
      [key: string]: any;
    };
  };
} & React.HTMLAttributes<HTMLElement>;

const ProductSection: React.FunctionComponent<Props> = props => {
  const isProduct = props.productId;
  const enabledStore = config.enabledStore;

  return (
    <Section className={styles.container}>
      <Container>
        <Row className={styles.row}>
          <Column xs={12} mdOffset={2} md={4} className={styles.imageColumn}>
            <GatsbyImage asset={props._rawImage.asset} className={styles.image} />
          </Column>
          <Column xs={12} mdOffset={1} md={7} className={styles.textColumn}>
            <H3 className={styles.text}>{props.title}</H3>
            <BlockContent blocks={props.body} className={styles.text} />
            {enabledStore && isProduct && (
              <CallToActionButton
                className="snipcart-add-item"
                data-item-id={props.productId}
                data-item-price={props.price}
                data-item-url={config.snipcartApiProductWebhooks}
                data-item-image={props._rawImage.asset.url}
                data-item-has-taxes-included="true"
                data-item-taxable="false"
                data-item-name={props.title}>
                Legg i handlevogn
              </CallToActionButton>
            )}
          </Column>
        </Row>
      </Container>
    </Section>
  );
};

export default ProductSection;
