import * as React from 'react';
import CallToActionLink from '../callToAction/CallToActionLink';
import Link from '../link/Link';
import ChecklistItem from '../checklist/ChecklistItem';
import styles from './blockContentSerializerBrown.module.css';
import GatsbyImage from '../gatsbyImage/GatsbyImage';

export default {
  types: {
    callToAction: (props: any) => (
      <CallToActionLink theme="brown" className={styles.callToAction} to={props.node.path}>
        {props.node.title}
      </CallToActionLink>
    ),
    image: (props: any) => {
      return <GatsbyImage asset={props.node.asset} className={styles.image} />;
    },
  },
  marks: {
    link: (props: any) => {
      return (
        <Link to={props.mark.href} className={styles.link}>
          {props.children}
        </Link>
      );
    },
  },
  listItem: (props: Props) => (
    <ChecklistItem className={styles.listItem}>{props.children}</ChecklistItem>
  ),
};
