import * as React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import TreatmentMenuTypes from '../components/TreatmentMenuTypes';
import { TreatmentType } from '../forces/types';
import TreatmentMenuList from '../components/TreatmentMenuList';
import SectionTitle from '../../../common/components/section/SectionTitle';
import { mapEdgesToNodes } from '../../../lib/helpers';
import CallToActionLink from '../../../common/components/callToAction/CallToActionLink';
import Routes from '../../../common/routing/Routes';
import styles from './TreatmentMenu.module.css';

const query = graphql`
  query TreatmentMenuQuery {
    treatmentTypes: allSanityTreatmentType(
      sort: { fields: order }
      filter: { hidden: { ne: true } }
    ) {
      edges {
        node {
          id
          title
          treatments: _rawTreatments(resolveReferences: { maxDepth: 5 })
        }
      }
    }
  }
`;

type Props = {
  title: string;
} & React.HTMLAttributes<HTMLElement>;
interface State {
  selectedIndex: number;
}

class TreatmentMenu extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      selectedIndex: 0,
    };
  }

  onTypeSelected(index: number) {
    this.setState({ selectedIndex: index });
  }

  render() {
    const { title, className } = this.props;
    const { selectedIndex } = this.state;
    return (
      <StaticQuery
        query={query}
        render={data => {
          const treatmentTypes = mapEdgesToNodes<TreatmentType>(data.treatmentTypes);
          const selectedTreatments = treatmentTypes[selectedIndex];
          return (
            <div className={className}>
              <SectionTitle className={styles.text}>{title}</SectionTitle>
              <TreatmentMenuTypes
                types={treatmentTypes}
                selectedIndex={selectedIndex}
                onTypeSelected={index => this.onTypeSelected(index)}
              />
              <TreatmentMenuList treatments={selectedTreatments.treatments} />
              <CallToActionLink to={Routes.Booking()} className={styles.bookButton} theme="dark">
                Book time
              </CallToActionLink>
            </div>
          );
        }}
      />
    );
  }
}

export default TreatmentMenu;
