import React, { useEffect } from 'react';
import { graphql } from 'gatsby';
import { mapEdgesToNodes } from '../../lib/helpers';
import GraphQLErrorList from '../components/graphql-error-list';
import BlockContent from '../components/blockContent/BlockContent';
import Layout from '../components/layout/Layout';
import Cart from '../components/cart/Cart';
import styles from './StoreTemplate.module.css';

export const query = graphql`
  query StorePageQuery($id: String!) {
    store: sanityStorePage(id: { eq: $id }) {
      body: _rawBody(resolveReferences: { maxDepth: 5 })
      products: _rawProduct(resolveReferences: { maxDepth: 5 })
    }
  }
`;
interface Props {
  data: any;
  errors: any;
}

const StorePage = (props: Props) => {
  const { data, errors } = props;
  if (errors) {
    return (
      <Layout>
        <GraphQLErrorList errors={errors} />
      </Layout>
    );
  }

  const body = data.store.body || null;
  return (
    <Layout className={styles.layout}>
      <Cart />
      {body && (
        <div className={styles.infoTextContainer}>
          <BlockContent blocks={body} className={styles.infoText} />
        </div>
      )}
      {data.store.products.map(product => (
        <BlockContent blocks={product} key={product._key} />
      ))}
    </Layout>
  );
};

export default StorePage;
