import React from 'react';
import LeftArrow from '../../../assets/images/Desktop_Arrow_White_Left.svg';
import RightArrow from '../../../assets/images/Desktop_Arrow_White.svg';
import GatsbyImage from '../gatsbyImage/GatsbyImage';
import styles from './Slider.module.css';

type Props = { images: Array<{ [key: string]: any }> } & React.HTMLAttributes<HTMLElement>;
interface State {
  currentIndex: number;
  loading: boolean;
}

export default class Slider extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);

    this.state = {
      currentIndex: 0,
      loading: false,
    };
  }

  goToPrevSlide = () => {
    const prevIndex =
      this.state.currentIndex == 0 ? this.props.images.length - 1 : this.state.currentIndex - 1;
    this.setState({
      loading: true,
      currentIndex: prevIndex,
    });
  };

  goToNextSlide = () => {
    const nextIndex =
      this.state.currentIndex == this.props.images.length - 1 ? 0 : this.state.currentIndex + 1;
    this.setState({
      loading: true,
      currentIndex: nextIndex,
    });
  };

  render() {
    if (!this.state.loading) {
      const prevIndex =
        this.state.currentIndex == 0 ? this.props.images.length - 1 : this.state.currentIndex - 1;
      const nextIndex =
        this.state.currentIndex == this.props.images.length - 1 ? 0 : this.state.currentIndex + 1;
      return (
        <div className={styles.slideshow}>
          <img className={styles.icon} src={LeftArrow} onClick={this.goToPrevSlide} />

          <div onClick={this.goToPrevSlide}>
            <GatsbyImage asset={this.props.images[prevIndex]} className={styles.prevImg} />
          </div>
          <GatsbyImage
            asset={this.props.images[this.state.currentIndex]}
            className={styles.mainImg}
          />
          <div onClick={this.goToNextSlide}>
            <GatsbyImage asset={this.props.images[nextIndex]} className={styles.nextImg} />
          </div>

          <img className={styles.icon} src={RightArrow} onClick={this.goToNextSlide} />
        </div>
      );
    }
    this.setState({ loading: false });
    return null;
  }
}
