import * as React from 'react';
import classNames from 'classnames';
import { graphql, StaticQuery } from 'gatsby';
import Section from '../../../common/components/section/Section';
import { Container, Row, Column } from '../../../common/components/grid/Grid';
import SectionTitle from '../../../common/components/section/SectionTitle';
import { mapEdgesToNodes } from '../../../lib/helpers';
import { Location } from '../types';
import LocationSelector from '../components/LocationSelector';
import { BodyText, BodyBolderText } from '../../../common/components/texts';
import Slider from '../../../common/components/slideshow/Slider';
import styles from './LocationsSection.module.css';

const query = graphql`
  query LocationsQuery {
    locations: allSanityClinic {
      edges {
        node {
          id
          name
          image: _rawImage(resolveReferences: { maxDepth: 5 })
          address {
            street
            postalCode
            city {
              name
            }
          }
          openingHours {
            monday {
              from
              to
            }
            friday {
              from
              to
            }
            saturday {
              from
              to
            }
            sunday {
              from
              to
            }
          }
        }
      }
    }
    sitedata: sanitySiteSettings {
      phoneNumber
      organizationNumber
      legalName
    }
  }
`;

type Props = {
  title: string;
  ctaLabel: string;
  image: {
    asset: {
      [key: string]: any;
    };
  };
} & React.HTMLAttributes<HTMLElement>;

interface State {
  selectedIndex: number;
}

class LocationsSection extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      selectedIndex: 0,
    };
  }

  onLocationSelected(index: number) {
    this.setState({
      selectedIndex: index,
    });
  }

  render() {
    const { className, ...props } = this.props;
    const { selectedIndex } = this.state;

    return (
      <StaticQuery
        query={query}
        render={data => {
          const locations = mapEdgesToNodes<Location>(data.locations);

          const { phoneNumber, organizationNumber, legalName } = data.sitedata;
          const selectedLocation = locations[selectedIndex];
          const gatsbyImages = selectedLocation.image.map(image => image.asset);
          return (
            <Section className={classNames(styles.container, className)} {...props}>
              <Container>
                <Row>
                  <Column xs={12} md={6}>
                    <div className={styles.map}>
                      <Slider images={gatsbyImages} />
                    </div>
                  </Column>
                  <Column xs={12} mdOffset={2} md={4}>
                    <SectionTitle>{props.title}</SectionTitle>
                    <LocationSelector
                      selectedIndex={selectedIndex}
                      locations={locations}
                      onLocationSelected={index => this.onLocationSelected(index)}
                    />
                    <BodyText>
                      Om du lurer p noe, send oss en melding p Facebook eller ring{' '}
                      <BodyBolderText>
                        <a className={styles.phoneNumber} href={`tel:+47${phoneNumber}`}>
                          {phoneNumber}
                        </a>
                      </BodyBolderText>
                    </BodyText>
                    <BodyText>
                      {legalName}
                      <br />
                      Org.nr. {organizationNumber}
                    </BodyText>
                  </Column>
                </Row>
              </Container>
            </Section>
          );
        }}
      />
    );
  }
}

export default LocationsSection;
