import * as React from 'react';
import classNames from 'classnames';
import { TreatmentType } from '../forces/types';
import SectionSubtitle from '../../../common/components/section/SectionSubtitle';
import styles from './TreatmentMenuTypes.module.css';

type Props = {
  types: Array<TreatmentType>;
  onTypeSelected: (index: number) => void;
  selectedIndex: number;
} & React.HTMLAttributes<HTMLElement>;

const TreatmentMenuTypes: React.FunctionComponent<Props> = ({
  className,
  types,
  onTypeSelected,
  selectedIndex,
  ...props
}) => {
  return (
    <div className={classNames(styles.container, className)} {...props}>
      {types.map((type, index) => (
        <SectionSubtitle
          key={type.id}
          className={classNames(styles.text, {
            [styles.textSelected]: index === selectedIndex,
          })}
          onClick={() => onTypeSelected(index)}
          data-title={type.title}>
          {type.title}
        </SectionSubtitle>
      ))}
    </div>
  );
};

export default TreatmentMenuTypes;
