import * as React from 'react';
import classNames from 'classnames';
import Section from '../../../common/components/section/Section';
import { Container, Row, Column } from '../../../common/components/grid/Grid';
import SectionTitle from '../../../common/components/section/SectionTitle';
import BlockContent from '../../../common/components/blockContent/BlockContent';
import styles from './GiftCardSection.module.css';

type Props = {
  title: string;
  ctaLabel: string;
  body: Array<{
    [key: string]: any;
  }>;
  image: {
    asset: {
      [key: string]: any;
    };
  };
} & React.HTMLAttributes<HTMLElement>;

const GiftCardSection: React.FunctionComponent<Props> = ({ className, ...props }) => {
  return (
    <Section
      backgroundImageAsset={props.image.asset}
      className={classNames(styles.container, className)}
      {...props}>
      <Container>
        <Row>
          <Column mdOffset={7} md={5}>
            <SectionTitle>{props.title}</SectionTitle>
            <BlockContent blocks={props.body} />
          </Column>
        </Row>
      </Container>
    </Section>
  );
};

export default GiftCardSection;
