import * as React from 'react';
import Section from '../../../common/components/section/Section';
import { Container, Row, Column } from '../../../common/components/grid/Grid';
import BlockContent from '../../../common/components/blockContent/BlockContent';
import GatsbyImage from '../../../common/components/gatsbyImage/GatsbyImage';
import blockContentSerializersBrown from '../../../common/components/blockContent/blockContentSerializerBrown';
import styles from './MembershipSection.module.css';

type Props = {
  body: Array<{ [key: string]: any }>;
  ctaLabel: string;
  title: string;
  image: {
    asset: {
      [key: string]: any;
    };
  };
} & React.HTMLAttributes<HTMLElement>;

const MembershipSection: React.FunctionComponent<Props> = props => {
  return (
    <Section className={styles.container}>
      <Container>
        <Row>
          <Column xs={12} md={6}>
            <BlockContent blocks={props.body} serializers={blockContentSerializersBrown} />
          </Column>
          <Column xs={12} mdOffset={1} md={5} className={styles.rightColumn}>
            <GatsbyImage asset={props.image.asset} className={styles.image} />
          </Column>
        </Row>
      </Container>
    </Section>
  );
};

export default MembershipSection;
