import * as React from 'react';
import classNames from 'classnames';
import Section from '../../../common/components/section/Section';
import Scroll from 'react-scroll';
import Routes from '../../../common/routing/Routes';
import { Container, Row, Column } from '../../../common/components/grid/Grid';
import TreatmentMenu from '../containers/TreatmentMenu';
import GatsbyImage from '../../../common/components/gatsbyImage/GatsbyImage';
import styles from './TreatmentMenuSection.module.css';

type Props = {
  title: string;
  image: {
    asset: {
      [key: string]: any;
    };
  };
} & React.HTMLAttributes<HTMLElement>;

const TreatmentMenuSection: React.FunctionComponent<Props> = ({ className, ...props }) => {
  return (
    <Scroll.Element key={Routes.TreatmentSection()} name={Routes.TreatmentSection()}>
      <Section className={classNames(styles.container, className)} {...props}>
        <Container>
          <Row>
            <Column xs={12} md={5} className={styles.leftColumn}>
              <GatsbyImage asset={props.image.asset} className={styles.image} />
            </Column>
            <Column mdOffset={2} md={5}>
              <TreatmentMenu title={props.title} />
            </Column>
          </Row>
        </Container>
      </Section>
    </Scroll.Element>
  );
};

export default TreatmentMenuSection;
