import * as React from 'react';
import Section from '../../../common/components/section/Section';
import { Container, Row, Column } from '../../../common/components/grid/Grid';
import { H2 } from '../../../common/components/texts';
import BlockContent from '../../../common/components/blockContent/BlockContent';
import GatsbyImage from '../../../common/components/gatsbyImage/GatsbyImage';
import Scroll from 'react-scroll';
import Routes from '../../../common/routing/Routes';
import styles from './OurCultureSection.module.css';

type Props = {
  body: Array<{ [key: string]: any }>;
  ctaLabel: string;
  title: string;
  image: {
    asset: {
      [key: string]: any;
    };
  };
} & React.HTMLAttributes<HTMLElement>;

const OurCultureSection: React.FunctionComponent<Props> = props => {
  return (
    <Scroll.Element key={Routes.AboutSection()} name={Routes.AboutSection()}>
      <Section className={styles.container}>
        <Container>
          <Row>
            <Column xs={12} mdOffset={1} md={4} className={styles.center}>
              <H2 className={styles.text}>{props.title}</H2>
              <BlockContent blocks={props.body} className={styles.text} />
            </Column>
            <Column xs={12} mdOffset={2} md={5} className={styles.rightColumn}>
              <GatsbyImage asset={props.image.asset} className={styles.ourCultureImage} />
            </Column>
          </Row>
        </Container>
      </Section>
    </Scroll.Element>
  );
};

export default OurCultureSection;
