import * as React from 'react';
import SanityBlockContent from '@sanity/block-content-to-react';
import blockContentSerializers from './blockContentSerializers';
import { deepMerge } from '../../../utils/object.utils';

type Props = {
  blocks: any;
  serializers?: { [key: string]: any };
} & React.HTMLAttributes<HTMLElement>;

const BlockContent: React.FunctionComponent<Props> = ({ serializers = {}, ...props }) => {
  return (
    <SanityBlockContent
      renderContainerOnSingleChild
      blocks={props.blocks}
      serializers={deepMerge(blockContentSerializers, serializers)}
      className={props.className}
    />
  );
};

export default BlockContent;
